.app {
   height: 100vh;
}

.wrapper {
   width: 100%;
   height: 100%;
   padding: 0 25px;
   margin-top: 50px;
   display: grid;
   grid-template-areas:
      "title title"
      "dd table"
      ". pages";
   align-content: start;
   row-gap: 25px;
   column-gap: 25px;
   grid-template-columns: min-content;

}

.title {
   grid-area: title;
   text-align: center;
   margin: 0;
}

.dropdowns {
   grid-area: dd;
   margin-top: 8px;
   display: flex;
   flex-direction: column;
   gap: 32px;
}

.input {
   border: 1px solid #dee2e6;
   box-sizing: border-box;
   font-family: inherit;
   font-size: 16px;
   font-weight: 400;
   line-height: 2;
   padding-left: 5px;
   width: 100%;
}

.button {
   border: none;
   padding-top: 5px;
   padding-bottom: 5px;
   border-radius: 5px;
   box-shadow: 0 4px 0 #dee2e6;
   transition: background-color .3s ease, box-shadow .3s ease;
}

.button:active {
   box-shadow: 0px 3px 0px #dee2e6;
}